import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { getDetails } from "../../actions/getDetails";
import { getPokemons } from "../../actions/getPokemons";

import "./Home.css";

const Home = (props) => {
  const [pokemons, setPokemons] = useState([]);

  useEffect(() => {
    setPokemons(props.state);
  }, [props.state, setPokemons]);
  useEffect(() => {
    setPokemons(props.state);
  }, [props.state, setPokemons]);

  return (
    <div className="App">
      <div>
        {" "}
        {props.loading ? (
          <div className="poke" key={props.pokeDetails.pokeId}>
          
             
              <div>
              {props.pokeDetails.pokeId<20000?<h4>#{props.pokeDetails.pokeId - 10000} </h4>: <h4>Creado #{props.pokeDetails.pokeId - 20000} </h4>}{" "}
              {props.pokeDetails.pokeId<20000?<Link to={`/pokemons/id/${props.pokeDetails.pokeId - 10000}`}>{props.pokeDetails.name}</Link>: <Link to={`/pokemons/id/${props.pokeDetails.pokeId}`}>{props.pokeDetails.name}</Link>}
              
              </div>
            
            <img src={props.pokeDetails.sprite} alt="Esperando!" />
            <ul>
              {props.pokeDetails.tipos.map((t) => (
                <li key={props.pokeDetails.tipos.indexOf(t)}>{t}</li>
              ))}
            </ul>
          </div>
        ) : (
          ""
        )}
      </div>
      <div className="App">
      {pokemons.length > 1 ? (
        pokemons.map((s) => (
          <div className="poke" key={s.pokeId}>
          <div>
              {s.pokeId<20000?<h4>#{s.pokeId - 10000} </h4>: <h4>Creado #{s.pokeId - 20000} </h4>}{" "}
              <Link to={`/pokemons/id/${s.pokeId - 10000}`}>{s.name}</Link>
              </div>
            <img src={s.sprite} alt="Esperando!" />
            <ul>
              {s.tipos.map((t) => (
                <li key={s.tipos.indexOf(t)}>{t}</li>
              ))}
            </ul>
           
          </div>
        ))
      ) : (
        <img
          className="pokegif"
          src="https://i.pinimg.com/originals/ba/0b/7a/ba0b7a611bc443b4e5f684101229d3b5.gif"
          alt=""
        />
      )}
      </div>
      <div>
     <button onClick={()=>{props.getPokemons()}}>MAS!</button>
     </div>
    </div>
   
  );
};
const mapStateToProps = (state) => {
  return {
    state: state.pokemonHome,
    pokeDetails: state.pokemonDetails,
    loading: state.loading,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
      getPokemons: () => dispatch(getPokemons()),
    getDetails: (id) => dispatch(getDetails(id)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
